import YikScroll from "./yik-scroll/index.vue";
import YikMaxView from "./yik-max-view";
import YikSign from "./yik-sign/index.vue";
import YikIsKeyboard from "./yik-is-keyboard";
import YikFocus from "./yik-directives/focus";
import YikPower from "./yik-directives/power";
import YikLazy from "./yik-directives/lazy";
import YikMarquee from "./yik-marquee";

import { yikLog } from "./yik-utils";

let components = [
  {
    name: "YikScroll",
    component: YikScroll,
  },
  {
    name: "YikMaxView",
    component: YikMaxView,
  },
  {
    name: "YikSign",
    component: YikSign,
  },
  {
    name: "YikIsKeyboard",
    component: YikIsKeyboard,
  },
  {
    name: "YikMarquee",
    component: YikMarquee,
  },
];
let directives = [
  {
    name: "YikFocus",
    directive: YikFocus,
  },
  {
    name: "YikPower",
    directive: YikPower,
  },
  {
    name: "YikLazy",
    directive: YikLazy,
  },
];

const install = (app) => {
  try {
    components.forEach((item) => {
      app.component(item.name, item.component);
    });
    directives.forEach((item) => {
      app.directive(item.name, item.directive);
    });
    yikLog("YikUi 指令和组件，已全局注册.");
  } catch (e) {
    yikLog("YikUi 指令和组件，全局注册失败.", "error");
  }
};
export default { install, YikScroll, YikMaxView, YikSign, YikFocus };
