<template>
  <canvas :width="width" :height="height" ref="yikSignRef"></canvas>
</template>
<script setup>
import {
  ref,
  onUnmounted,
  defineProps,
  defineExpose,
  watchEffect,
  nextTick,
} from "vue";
import Sign from "./index.js";
const yikSignRef = ref(null);
let lineList = [];
let sign = null;
const props = defineProps({
  width: {
    type: Number,
    default: 500,
  },
  height: {
    type: Number,
    default: 300,
  },
  color: {
    type: String,
    default: "#000",
  },
  lineWidth: {
    type: Number,
    default: 3,
  },
  bg: {
    default: "#fff",
  },
  value: {
    type: Array,
    default: () => [],
  },
});
const stop = watchEffect(() => {
  if (props.lineWidth && props.color && props.bg) {
    nextTick(() => {
      createSign();
    });
  }
});
const createSign = () => {
  if (sign) {
    lineList = [];
    sign = null;
  }
  sign = new Sign({
    el: yikSignRef.value,
    color: props.color,
    lineWidth: props.lineWidth,
    bg: props.bg,
    watch: (data) => {
      lineList = [...data];
    },
  });
  if (props.value) {
    lineList = [...props.value];
    sign.setEchoArr(props.value);
  }
};
onUnmounted(() => {
  stop();
  if (sign) sign = null;
});
defineExpose({
  save: () => {
    return sign.save();
  },
  clear: () => {
    lineList = [];
    sign.clear();
  },
  getLine: () => {
    return lineList;
  },
});
</script>
