 * @FilePath: \yik-ui\packages\yik-is-keyboard\index.vue
 * @Description: 监听移动端键盘是否弹起
-->
<template>
  <slot v-if="showSlot"></slot>
</template>
<script setup>
import { defineEmits, onMounted, shallowRef, useSlots } from "vue";
const showSlot = shallowRef(true);
const slots = useSlots();
const emit = defineEmits(["onOpen", "onClose"]);
const isPhone = () => {
  const mobileFlags = [
    /AppleWebKit.*Mobile.*/, // 移动终端
    /\(i[^;]+;( U;)? CPU.+Mac OS X/, // ios终端
    /Android/, // 安卓终端
    /iPhone/, // iPhone
    /iPad/, // iPad
  ];
  const ua = navigator.userAgent;
  for (let flag of mobileFlags) {
    if (flag.test(ua)) {
      return {
        phone: true,
        message: flag,
      };
    }
  }
  return {
    phone: false,
  };
};
const isSlotsHidden = (flag) => {
  if (slots.default) {
    showSlot.value = flag;
  }
};
const isKeyboard = () => {
  const innerHeight = window.innerHeight;
  if (isPhone().message == "/iPhone/") {
    window.addEventListener("focusin", () => {
      emit("onOpen"); //开启
      isSlotsHidden(false);
    });
    window.addEventListener("focusout", () => {
      emit("onClose"); //关闭
      isSlotsHidden(true);
    });
  } else {
    window.addEventListener("resize", () => {
      const newInnerHeight = window.innerHeight;
      if (innerHeight > newInnerHeight) {
        emit("onOpen"); //开启
        isSlotsHidden(false);
      } else {
        emit("onClose"); //关闭
        isSlotsHidden(true);
      }
    });
  }
};
onMounted(() => {
  isKeyboard();
});
</script>
<style lang="less" scoped></style>
