 * @Description: 滚动条监听
-->
<template>
  <div class="yik-ui-page" ref="yikUiPageRef">
    <slot></slot>
    <slot name="loading"></slot>
  </div>
</template>
<script setup>
import Scroll from "./index";
import {
  onUnmounted,
  ref,
  onMounted,
  defineEmits,
  defineProps,
  watch,
} from "vue";
const emit = defineEmits(["onBottom", "onTop", "onWatch"]);
const props = defineProps({
  scroll: {
    type: Number,
    default: 0,
  },
});
const yikUiPageRef = ref(null);
let scroll = null;
onMounted(() => {
  yikUiPageRef.value.style["overflow-y"] = "auto";
  scroll = new Scroll({
    el: yikUiPageRef.value,
    watchBottom: () => {
      emit("onBottom");
    },
    watchTop: () => {
      emit("onTop");
    },
    watch: (data) => {
      emit("onWatch", data);
    },
  });
  scroll.setTop(props.scroll);
  watch(
    () => props.scroll.value,
    (val) => {
      scroll.setTop(val);
    }
  );
});
onUnmounted(() => {
  if (scroll) scroll = null;
});
</script>
