export default class Sign {
  constructor({
    el, // canvas dom
    lineWidth = 3, // 线条宽度
    color = "#0088ff", //线条颜色
    bg, // 背景颜色或者背景图片
    watch,
  }) {
    this.canvas = el; // 获取 canvas dom
    this.ctx = this.canvas.getContext("2d");
    this._watch = watch;
    this.isDraw = false;
    this.lineWidth = lineWidth;
    this.color = color;
    this.bg = bg;
    this._array = [];
    this.onInit(); // 初始话相关事件
    this.drawing(); // 开启绘制功能
    this.initBg(); // 初始化背景
  }
  // 回显
  setEchoArr(echoArr) {
    if (echoArr && Array.isArray(echoArr)) {
      this._array.push(...echoArr);
      echoArr.forEach((item) => {
        if (item.length)
          item.forEach((_item) => {
            if (_item instanceof Object) {
              this.ctx.lineTo(_item.x, _item.y);
              this.ctx.lineJoin = "round";
              this.ctx.lineCap = "round";
              this.ctx.lineWidth = this.lineWidth;
              this.ctx.strokeStyle = this.color;
              this.ctx.stroke();
            }
          });
      });
    } else {
      console.error("不是集合！");
    }
  }
  //保存
  save() {
    return this.canvas.toDataURL("image/png"); //转换城图片的流
  }
  //清楚绘制
  clear() {
    this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    this.initBg();
  }
  //设置背景色
  initBg() {
    if (this.bg)
      if (this.bg.indexOf("http") != -1) {
        const img = new Image();
        img.setAttribute("crossOrigin", "Anonymous");
        img.src = this.bg;
        img.onload = () => {
          this.ctx.drawImage(
            img,
            0,
            0,
            this.ctx.canvas.width,
            this.ctx.canvas.height
          );
        };
      } else {
        this.ctx.fillStyle = this.bg;
        this.ctx.fillRect(0, 0, this.ctx.canvas.width, this.ctx.canvas.height);
      }
  }
  //开始绘制
  drawing() {
    // 阻止页面拖动
    this.canvas.onmousemove = (e) => {
      if (this.isDraw) {
        const x = e.pageX - this.canvas.offsetLeft;
        const y = e.pageY - this.canvas.offsetTop;
        this._array[this._array.length - 1].push({
          x,
          y,
        });
        this.ctx.lineTo(x, y);
        this.ctx.lineJoin = "round";
        this.ctx.lineCap = "round";
        this.ctx.lineWidth = this.lineWidth;
        this.ctx.strokeStyle = this.color;
        this.ctx.stroke();
      }
    };
    this.canvas.ontouchmove = (e) => {
      if (this.isDraw) {
        const x = e.touches[0].pageX - this.canvas.offsetLeft;
        const y = e.touches[0].pageY - this.canvas.offsetTop;
        this._array[this._array.length - 1].push({
          x,
          y,
        });
        this.ctx.lineTo(x, y);
        this.ctx.lineJoin = "round";
        this.ctx.lineCap = "round";
        this.ctx.lineWidth = this.lineWidth;
        this.ctx.strokeStyle = this.color;
        this.ctx.stroke();
      }
    };
  }
  // 初始化
  onInit() {
    // 手指按下
    this.canvas.ontouchstart = () => {
      this._array.push([]);
      this.isDraw = true;
      this.ctx.beginPath();
    };
    // 手指抬起
    this.canvas.ontouchend = () => {
      this.isDraw = false;
      this.ctx.closePath();
      if (this._watch) this._watch(this._array);
    };
    // 鼠标按下
    this.canvas.onmousedown = () => {
      this._array.push([]);
      this.isDraw = true;
      this.ctx.beginPath();
    };
    // 鼠标抬起
    window.onmouseup = () => {
      this.isDraw = false;
      this.ctx.closePath();
    };
    // 鼠标抬起
    this.canvas.onmouseup = () => {
      this.isDraw = false;
      this.ctx.closePath();
      if (this._watch) this._watch(this._array);
    };
  }
}
