<template>
  <div
    ref="maxView"
    class="yik-ui-max-view"
    :style="{
      width,
      height,
      transformOrigin: '' + (isCover ? 'center' : 'left') + ' top',
    }"
  >
    <slot></slot>
  </div>
</template>

<script setup>
import {
  defineProps,
  ref,
  onMounted,
  onBeforeUnmount,
  getCurrentInstance,
} from "vue";
const maxView = ref(null);
let timeout = null;
const that = getCurrentInstance();
const props = defineProps({
  width: {
    type: String,
    default: "1920px",
  },
  height: {
    type: String,
    default: "1080px",
  },
  isCover: {
    type: Boolean,
    default: false,
  },
});
const zoomView = () => {
  const parentNode = that.refs.maxView.parentNode;
  let view = maxView.value,
    viewWidth = parseInt(view.style.width),
    viewHeight = parseInt(view.style.height);
  if (props.isCover) {
    parentNode.style.width = window.innerWidth + "px";
    parentNode.style.height = window.innerHeight + "px";
    parentNode.style.display = "flex";
    parentNode.style.justifyContent = "center";
    const scale =
      window.innerWidth / window.innerHeight < viewWidth / viewHeight
        ? window.innerWidth / viewWidth
        : window.innerHeight / viewHeight;
    view.style.transform = `scale(${scale})`;
  } else {
    let w = window.innerWidth / viewWidth;
    let h = window.innerHeight / viewHeight;
    view.style.transform = `scale(${w},${h})`;
  }
};
onMounted(() => {
  document.body.style.overflow = "hidden";
  document.body.style.margin = "0";
  maxView.value.style.transition = "transform 0.5s";
  maxView.value.style["box-sizing"] = "border-box";
  zoomView();
  window.onresize = () => {
    if (timeout) clearTimeout(timeout);
    timeout = setTimeout(() => {
      zoomView();
    }, 500);
  };
}),
  onBeforeUnmount(() => {
    window.onresize = null;
  });
</script>
