export default {
  mounted(el, binding) {
    const modifiers = binding.modifiers;
    const { values, value, callback } = binding.value;
    let count = 0;
    // 循环匹配
    values.forEach((item) => {
      if (item == value) {
        count++;
      }
    });
    if (count == 0) {
      // v-show 单独隐藏属性 v-if直接删除属性  默认v-if
      if (modifiers["v-show"]) {
        el.style.display = "none";
      } else if (modifiers["v-if"]) {
        el.remove();
      } else {
        el.remove();
      }
    }
    if (callback) callback(count);
  },
};
