/**
 * @description:
 * @param {*} message 消息
 * @param {*} type error错误
 * @return {*}
 */
export const yikLog = (message, type) => {
  let backgroundColor = "";
  if (type == "error") {
    backgroundColor = "#ff362b";
  } else {
    backgroundColor = "#1c8eff";
  }
  console.log(
    "%c" + message,
    `padding:4px 15px;background-color: ${backgroundColor};color:#fff;border-radius: 10px;`
  );
};
